import React from "react";
import styled from "styled-components";

import { Box } from "../../0-base/FlexBox";

interface NavigationBoxProps {
  children?: React.ReactNode;
}

const StyledNavigationBox = styled(Box).attrs({
  as: "nav",
})`
  width: 280px;
  min-height: calc(100vh);
  box-shadow: 0px 2.4px 28px rgba(33, 40, 50, 0.15);
`;

export default function NavigationBox({
  children,
}: NavigationBoxProps): JSX.Element {
  return <StyledNavigationBox>{children}</StyledNavigationBox>;
}
